
#resumePreview {
  background: #fff;
  -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
          box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
  padding: 0;
  color: #333;
  line-height: 1.2;
  overflow: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -ms-flex-line-pack: justify;
      align-content: space-between;
}
#resumePreview * {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-variant: normal;
    font-weight: normal;
}
#resumePreview ol {
    list-style: none;
}
#resumePreview p {
    white-space: pre-line;
}
#resumePreview .top {
    width: 100%;
    background: #3e6391;
    line-height: 46px;
    -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
            box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
    z-index: 10;
    overflow: hidden;
}
#resumePreview .top > h3 {
      color: #fff;
      float: left;
      margin-left: .5em;
}
#resumePreview .top > .button {
      float: right;
      font-size: 16px;
      color: #e0e0e0;
      text-decoration: none;
      margin-right: 1em;
}
#resumePreview .top > .button:hover {
        color: #fff;
}
#resumePreview .resume {
    width: 70%;
    min-height: 100vh;
}
#resumePreview .aside {
    width: 30%;
    background: #f0efe9;
    text-align: center;
    overflow: hidden;
}
#resumePreview .aside > .avatar {
      width: 150px;
      height: 150px;
      margin: .5em auto;
      border-radius: 50%;
      background: #fff;
      padding: 3px;
      text-align: center;
}
#resumePreview .aside > .avatar > img {
        width: 144px;
        height: 144px;
        border-radius: 50%;
        vertical-align: middle;
}
#resumePreview section[data-name="profile"], #resumePreview section[data-name="skills"] {
    margin: 1.5em 0;
}
#resumePreview section[data-name="profile"] ol, #resumePreview section[data-name="skills"] ol {
      border-bottom: none;
}
#resumePreview section[data-name="profile"] > .aside-title, #resumePreview section[data-name="skills"] > .aside-title {
      width: 85%;
      margin: .8em auto;
      font-size: 1.2em;
      padding: .3em .8em;
      border: 2px solid #3e6391;
      color: #3e6391;
}
#resumePreview section[data-name="profile"] > h1, #resumePreview section[data-name="skills"] > h1 {
      margin: .1em 0;
      font-size: 1.8em;
      color: #3e6391;
}
#resumePreview section[data-name="profile"] > h4, #resumePreview section[data-name="skills"] > h4 {
      margin: .1em 0 .2em 0;
}
#resumePreview section[data-name="profile"] > .person-info, #resumePreview section[data-name="skills"] > .person-info {
      width: 85%;
      margin: 0 auto;
      text-align: left;
}
#resumePreview section[data-name="profile"] > .person-info > li, #resumePreview section[data-name="skills"] > .person-info > li {
        font-size: 14px;
        margin: .2em .5em 0 0;
}
#resumePreview section[data-name="skills"] > ol {
    width: 85%;
    margin: 0 auto;
}
#resumePreview section[data-name="skills"] > ol > li {
      font-size: 14px;
      margin: .2em 0 .5em 0;
      text-align: left;
}
#resumePreview section[data-name="skills"] > ol > li > p.skill-detail {
        padding: .3em;
        font-size: 14px;
        text-align: left;
        overflow: hidden;
}
#resumePreview section[data-name="skills"] > ol > li > p.skill-detail > span:first-child {
          float: left;
          width: 40%;
}
#resumePreview section[data-name="skills"] > ol > li > p.skill-detail > span:nth-child(2) {
          margin-left: .8em;
          float: left;
          width: 50%;
}
#resumePreview section > h2 {
    display: inline-block;
    padding: .2em;
    margin: 1em 0 .5em 0;
    font-size: 1.2em;
}
#resumePreview section > h2 > b.icon-img {
      display: inline-block;
      width: 18px;
      height: 18px;
      border: 1px dotted #3e6391;
      border-radius: 50%;
      background: transparent;
      vertical-align: middle;
      margin-left: .5em;
      margin-top: -3px;
}
#resumePreview section > ol {
    padding: .8em;
    border-bottom: 1px solid #e0e0e0;
}
#resumePreview section > ol > li {
      margin-bottom: .8em;
}
#resumePreview section[data-name="workHistory"] li,
  #resumePreview section[data-name="awards"] li {
    margin-bottom: 1.5em;
}
#resumePreview section[data-name="workHistory"] li > h4,
    #resumePreview section[data-name="awards"] li > h4 {
      display: inline-block;
      padding: .5em 0;
      margin-right: 2em;
      color: #446a9a;
}
#resumePreview section[data-name="workHistory"] li > h4h4:last-child(),
      #resumePreview section[data-name="awards"] li > h4h4:last-child() {
        margin-right: 0;
}
#resumePreview section[data-name="workHistory"] li > p,
    #resumePreview section[data-name="awards"] li > p {
      font-size: 14px;
}
#resumePreview section[data-name="projects"] li {
    padding-bottom: 1em;
    margin-bottom: 1.5em;
    border-bottom: 1px dotted #e0e0e0;
}
#resumePreview section[data-name="projects"] li > h4 {
      padding: .3em 0;
      margin-right: 2em;
      color: #446a9a;
}
#resumePreview section[data-name="projects"] li > h4h4:last-child() {
        margin-right: none;
}
#resumePreview section[data-name="projects"] li > p {
      padding-top: .2em;
      font-size: 14px;
}
#resumePreview section[data-name="projects"] li:last-child {
    border-bottom: none;
}
#resumePreview section[data-name="education"] ol > li > h3 > p {
    display: inline-block;
    margin: .2em .8em;
    font-size: .8em;
}
#resumePreview section[data-name="education"] li {
    line-height: 1.5;
}
#resumePreview section[data-name="contacts"] li {
    color: #3e6391;
    font-size: .8em;
    float: left;
    margin: 0 0 1.5em 1em;
}
#resumePreview section[data-name="contacts"] ol {
    border-bottom: none;
}



